// mint-ui
import MintUI from "mint-ui";
Vue.use(MintUI);
//3:axios main.js
//3.1:axios
import axios from "axios"
//3.2:
axios.defaults.withCredentials=true
//3.3:axiosVue
Vue.prototype.axios = axios
// 4.
// 4.1
// 4.2
Vue.filter("datetimeFilter",function(val){
  // (1)
  var now=new Date(val);
  // (2)
  var y=now.getFullYear();
  var m=now.getMonth()+1;
  var d=now.getDate();
  // (3)
  var h=now.getHours();
  var mi=now.getMinutes();
  var s=now.getSeconds();
  // (4)
  m<10&&(m="0"+m);
  d<10&&(d="0"+d);
  // (5)
  return `${y}-${m}-${d} ${h}:${mi}:${s}`;
})
Vue.filter("unameFilter",function(val){
  var str = String(val)
  var len = str.length;
  var prev,next;
  prev = str.slice(-len,-8);
  next = str.slice(-4);
  str = prev+"****"+next;
  return str;
})
// // eval()
// Vue.filter("evalFilter",function(val){
//   val=eval(val);
//   return val[0];
// })


// 
router.beforeEach((to, from, next) => {    
  // chrome
  document.body.scrollTop = 0
  // firefox
  document.documentElement.scrollTop = 0
  // safari
  window.pageYOffset = 0
  next()
})

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')



