<template>
    <div class="container">
        <div id="header">
		    <div class="flex-container">
                <a @click="back"><span class="mui-icon mui-icon-back"></span></a>
            </div>
        </div>
        <div id="banner">
            <!-- <img :src="detail.pimg"> -->
            <a @click="$router.push('/photo?pid='+tid)">
                <mt-swipe>
                        <mt-swipe-item v-for="(item,index) of banner" :key="item.index">
                            <img :src="item">
                        </mt-swipe-item>
                </mt-swipe>
            </a>
        </div>
        <div id="title">
            <h1>{{detail.pname}}</h1>
        </div>
        <div id="price">
            <div class="price_left">
                <p class="price_title">     <span class="price_sign">&nbsp;  <span class="price_price"> {{detail.price}} </span>  &nbsp;&nbsp;<a class="price_ask" href="#modal">?</a></span>
                </p>
            </div>
