                <p class="mui-content-padded mb-0"></p>
            </div>
        </div>
        <div id="evaluate_wrapper">
            <div class="evaluate_left">
                <span></span>
                <span>{{detail.pticket}}</span>
            </div>
            <div class="evaluate_right">
                <span></span>
                <span>{{(detail.pgrade/10).toFixed(1)}}</span>
            </div>
        </div>
        <div id="recommend">
            <h1></h1>
            <p v-for="(item,index) of recommend" :key="index"><span></span> {{item}} </p>
            <!-- <p><span></span>3</p> 0
            <p><span></span></p>
            <p><span></span> 76H5REST88</p>
            <p><span></span> </p> -->
        </div>
        <div id="product_introduct">
          <div class="product_tab" :style="pstyle">
                <a href="javascript:void(0)" @click="goAnchor('#anchor-'+index)" v-for="(item,index) of list"> {{item}} </a>
            </div>
            <div id="anchor-0" class="item">
                <h1></h1>
                <div class="item_content" v-if="feature">
                    <h2></h2>
                    <p v-for="(item,index) of featrue_ture" :key="index">{{index+1}} . {{item}}</p>
                    <!-- <p>1.55-</p>
                    <p>2.60</p>
                    <p>3.30-</p>
                    <p>4.30</p>
                    <p>5.80</p>
                    <p>6.35</p>
                    <p>7.25**</p>
                    <p>8.50FISH & CHIPS +-Pasty</p>
                    <p>9.200</p> -->
                </div>
                <div class="detail_img">
                    <img :src="item" v-for="(item,index) of detail_img" :key="index">
                </div>
            </div>
            <div id="anchor-1" class="item">
                <h1></h1>
                <div class="item_content">
                    <p></p>
                    <p></p>
                    <p></p>
                    <p></p>
                    <p></p>
                    <p></p>
                    <p></p>
                    <p></p>
                </div>
            </div>
            <div id="anchor-2" class="item">
                <h1></h1>
                <div class="item_content font_black">
                    <p><b> : </b></p>
                    <p>1.:/,</p>
                    <p>2.:2</p>
                    <p>3.:</p>
                    <p>4.:</p>
                    <p>5.:</p>
                    <p>6.:0~4,</p>
                    <p>7.:200/</p>
                </div>
                <div class="item_content font_black">
                    <p><b> : </b></p>
                    <p>1.:</p>
                    <p>2.:</p>
                    <p>3.:,,,</p>
                    <p>4.:</p>
                    <p>5.:120/</p>
                </div>
            </div>
            <div id="anchor-3" class="item">
                <h1></h1>
                <div class="item_content font_black">
                    <p>____/</p>
                    <p>____/</p>
                    <p>____/</p>
                    <p></p>
                    <p></p>
                    <p></p>
                    <p></p>
                    <p></p>
                    <p>207</p>
                    <p></p>
                    <p></p>
                    <p></p>
                </div>
                <div class="item_content font_black">
                    <h2></h2>
                    <p><b> </b></p>
                    <p>1.</p>
                    <p> </p>
                    <p>1 </p>
                    <p>2</p>
                    <p>3 </p>
                    <p>4</p>
                    <p>5 </p>
                    <p>650/</p>
                    <p>7 </p>
                    <p>8 </p>
                    <p><b> </b></p>
                    <p>1. 70</p>
                    <p>2.80</p>
                    <p>3.6565</p>
                    <p>4.7070</p>
                    <p><b> </b></p>
                    <p>1.18</p>
                    <p>2.18</p>
                    <p><b> </b></p>
                    <p> </p>
                </div>
            </div>
        </div>


        <div id="reserve">
            <span class="mui-icon-extra mui-icon-extra-heart-filled" v-if="islike" id="like" @click="changeLike"></span>
            <span class="mui-icon-extra mui-icon-extra-heart" v-else  @click="changeLike"></span>
            <div class="button" @click="subscribe">
                
            </div>
        </div>
    </div>

</template>
<script>
export default {
    data() {
        return {
            detail:[],
            banner:[],
            recommend:[],
            feature:false,//
            featrue_ture:[],
            detail_img:[],
            list:["","","",""],
            pstyle:{position:"fixed"},
            pastyle:{},
            pbstyle:{position:"fixed",top:"0px","z-index":1000},
            islike:false,
            tid:"",
            uname:"",
        }
    },
    created() {
        this.get_detail()
    },
    mounted() {
        window.addEventListener("scroll", this.handleScroll);
    },
    methods: {
        back(){
            this.$router.go(-1);//
        },
        get_detail(){
            this.tid=this.$route.query.pid;
            var url="product/detail?pid="+this.$route.query.pid;
            this.axios.get(url).then(result=>{
                this.detail=result.data[0];
                // console.log(eval(this.detail.pimg))
                this.banner=eval(this.detail.pimg);
                // console.log(this.detail.precommend);
                this.recommend=eval(this.detail.precommend);
                if(this.detail.pfeature==null){
                    this.feature=false;
                }else{
                    this.featrue_ture=eval(this.detail.pfeature);
                    this.feature=true;
                }
                this.detail_img=eval(this.detail.pdetails_img)
            })
        },
        goAnchor(selector) {
       var anchor = this.$el.querySelector(selector);
       document.documentElement.scrollTop = anchor.offsetTop;
      },
        handleScroll(){
            let scrollTop = window.pageYOffset || document.documentElement.scrollTop || 	document.body.scrollTop;
            if(scrollTop>600){
                this.pstyle=this.pbstyle;
            }else{
                this.pstyle=this.pastyle;
            }
        },
        changeLike(){
            var url="user/hasUname"
            this.axios.get(url).then(result=>{
                if(result.data.code==-1){
                    this.$toast(result.data.msg);
                    setTimeout(()=>{
                        this.$router.push('/login?uid=0');
                    },2000);
                }else{
                    this.uname=result.data;
                    if(this.islike==false){
                        var url1="user/add_collect?tid="+this.tid+"&uname="+this.uname;
                        this.axios.get(url1).then(result=>{
                            if(result.data.code==1){
                                this.$toast(result.data.msg);
                                this.islike=true;
                            }else if(result.data.code==0){
                                this.$toast(result.data.msg);
                                this.islike=false;
                            }
                        })
                    }else{
                        var url1="user/del_collect?tid="+this.tid;
                        this.axios.get(url1).then(result=>{
                            if(result.data.code==1){
                                this.$toast(result.data.msg);
                                this.islike=false;
                            }
                        })
                    }
                }
            })
        },
        subscribe(){
           var url="user/hasUname"
            this.axios.get(url).then(result=>{
                if(result.data.code==-1){
                    this.$toast(result.data.msg);
                    setTimeout(()=>{
                        this.$router.push('/login?uid=0');
                    },2000);
                }else{
                    this.uname=result.data;
                    var url1="user/add_order?tid="+this.tid+"&uname="+this.uname;
                    this.axios.get(url1).then(result=>{
                        if(result.data.code==1){
                            this.$toast(result.data.msg);
                        }else if(result.data.code==0){
                            this.$toast(result.data.msg);
                        }
                    })
                }
            })
        }
    },
}
</script>
<style scoped>
    .container{
        position: relative;
        padding-bottom: 50px;
    }
    #header{
        position: fixed;
        z-index: 999;
        width: 100%;
        height: 45px;
        background:transparent;
    }
    #header .flex-container{
        position: absolute;
        top: 0px;
        left: 0px;
        width: 100%;
        height: 100%;
    }
    #header .flex-container a{
        margin-left: 20px;
        margin-top: 10px;
        color: #fff;
        display: inline-block;
        width: 30px;
        height: 30px;
        background: #0c0e0f;
        opacity: 0.5;
        border-radius: 50%;
        line-height: 0px;
    }
    #header .flex-container a span{
        display: inline-block;
        font-size: 26px;
        margin-top: 1px;
        margin-left: 1px;
    }
    #banner{
        width: 100%;
        height: 210px;
        background: #ccc;
    }
    #banner img{
        width: 100%;
        height: 100%;
    }
    #title h1{
        background: #fff;
        padding: 10px 15px;
        font-size: 16px;
        line-height: 22px;
        font-weight: bold;
        color: #404040;
        margin: 0px;
    }
    #price{
        width:100%;
        height: 60px;
        background: #F2E9E6;
        line-height: 60px;
    }
    #price .price_left{
        float: left;
        width: 67%;
        height: 100%;
    }
    #price .price_left .price_title{
        font-size: 12px;
        margin-left: 15px;
        margin-bottom: 0px;
        color: #222;
        height: 100%;
    }
    #price .price_left .price_sign{
        font-size: 12px;
        color: #f60;
    }
    #price .price_left .price_price{
        font-size: 22px;
        font-weight: bold;
    }
    #price .price_left .price_ask{
        font-size: 14px;
        color: #fff;
        background: #f60;
        font-weight: bold;
        padding: 0 4px;
        border-radius: 50%;
    }
    #price .price_right{
        float: left;
        width:33%;
        height: 100%;
        background:url("../img/price_sign.png");
        background-size: 100% 100%;
    }
    #evaluate_wrapper{
        width: 100%;
        background-color: #fbf9fa;
        display: flex;
        padding: 6px 0px;
        font-size: 12px;
    }
    #evaluate_wrapper div{
        width: 50%;
        text-align: center;
        line-height: 16px;
    }
    #evaluate_wrapper>div>span{
        display: inline-block;
        line-height: 16px;
        height: 16px;
        color: #999;
    }
    #evaluate_wrapper div.evaluate_right{
        border-left: 1px solid #999;
    }
    #evaluate_wrapper>.evaluate_left>span:last-child{
        display: inline-block;
        color: #333;
    }
    #evaluate_wrapper>.evaluate_right>span:last-child{
        display: inline-block;
        color:#f60;
    }
    #recommend{
        margin: 20px 0;
        padding: 8px;
        width: 100%;
        /* height: 300px; */
        background: #fff;
    }
    #recommend h1{
        border-left: 3px solid #f60;
        padding-left: 15px;
        height: 20px;
        font-size: 20px;
        font-weight: normal;
    }
    #recommend p{
        margin-left:10px;
        color: #666;
        font-size: 12px;
        margin-bottom: 0px;
    }
    #recommend p span{
        color: #f60;
    }
    #product_introduct .product_tab{
        width: 100%;
        height:40px;
        line-height: 40px;
        background: #404040;
        display: flex;
        justify-content: space-around;
    }
    #product_introduct .product_tab a{
        color: #F60;
        font-size: 14px;
    }
    #product_introduct .item{
        background: #fff;
        padding: 40px 15px 20px 15px;
        /* border-top: 1px solid #999; */
    }
    #product_introduct .item  h1{
        border-left: 3px solid #f60;
        height: 16px;
        font-size: 16px;
        padding-left: 15px;
        font-weight: normal;
        margin-bottom: 7px;
    }
    #product_introduct .item .item_content h2{
        font-size: 16px;
        font-weight: normal;
        padding: 10px 0;
    }
    #product_introduct .item .item_content p{
        color: #f60;
        font-size: 13px;
        margin-bottom: 0px;
        line-height: 1.8;
        text-align: justify;
    }
    #product_introduct .item .detail_img img{
        width: 100%;
        height: auto;
    }
    #product_introduct .item .font_black p{
        font-size: 14px;
        line-height: 26px;
        color: #666;
    }
    #product_introduct .item .item_content p b{
        color: #333;
        background: #999;
        margin: 20px 0;
    }




    #reserve {
        position: fixed;
        bottom: 0px;
        width:100%;
        height: 50px;
        line-height: 50px;
        background: #F60;
        text-align: center;
        color: #fff;
        font-weight: bold;
        display: flex;
        justify-content: space-around;
        align-items: center;
    }
    /* #reserve #collect{ */
        /* line-height: 50px; */
    /* } */
    #reserve span{
        font-size: 35px;
        color: red;
    }
    #reserve .button{
        width: 70%;
        background: red;
        height: 40px;
        line-height: 40px;
        padding: 0 50px;
        border-radius: 3px;
    }
</style>


